import * as React from "react";
import ClassNames from "classnames";
const styles = require("./index.less");
export default class ChannelHero extends React.Component {
    constructor(props) {
        super(props);
    }
    render() {
        const { channel } = this.props;
        if (!channel) {
            return null;
        }
        return (React.createElement("header", { className: styles.hero, style: { backgroundColor: channel.color } },
            React.createElement("div", { className: ClassNames("container", [styles.container]) },
                React.createElement("div", { className: styles.inner },
                    React.createElement("h2", { className: "Hero-title" }, channel.title),
                    React.createElement("p", { className: "Hero-subtitle" }, channel.description)))));
    }
}



