import * as React from "react";
import Header from "components/header";
import ChannelView from "views/channel";
import GlobalStore from "store/GlobalStore";
import ChannelStore from "store/HomeStore";
import { IS_NODE } from "../../../env";
export default class ChannelEntry extends React.Component {
    constructor(props) {
        super(props);
        if (!IS_NODE) {
            const { location, match } = props;
            GlobalStore.getInstance({ location, match, cookies: "" });
        }
    }
    render() {
        return (React.createElement("div", null,
            React.createElement(Header, null),
            React.createElement("main", { className: "app-content" },
                React.createElement(ChannelView, null))));
    }
}
// SSR 在入口组件中获知Store类并初始化用于实例注入
ChannelEntry.STORE_CLASSES = [GlobalStore, ChannelStore];



